#cp-container{
  display: grid;
  margin-top: 0;
  grid-template-columns: repeat(2,1fr);
  align-items: center;
  justify-content: center;
}
 .cp3 ,.cp4{
   
  padding: 2%;
  margin:2% 10%;
  border-radius: 20px;
  box-shadow: 5px 5px 20px #013430;
  display: flex;
  justify-content: space-around;
  align-items:center ;
  text-align: justify;
}
.cp3{
  grid-column-start: 1;
  grid-column-end:2;
}
.cp4{
  grid-column-start: 2;
  grid-column-end:3;
}
.cp1, .cp2{
  
  padding: 2%;
  margin:1% 4%;
  /* border:2px solid black; */
  border-radius: 20px;
  box-shadow: 5px 5px 20px #013430;
  grid-template-columns: repeat(3,1fr);
  grid-column-start: 1;
  grid-column-end:3;
  display:flex;
  justify-content: center;
  align-items:center ;
  text-align: justify;
}

.icon{
  /* margin-bottom: 10%; */
  margin:2%;
  /* flex:1 1 0px; */
}



@media  screen and (max-width:1450px) {
  #cp-container{
    grid-template-columns: repeat(1,1fr);
  }
  #cp-container div{
    margin-bottom: 5%;
  }
  .cp1, .cp2{
    flex-direction: column;
  }
  .cp3, .cp4{
    grid-column-start: 1;
    grid-column-end:3;
  }
  .charts{
    width:90%;
  }
  .profbtn{
    width:100%;
    font-size: 0.3rem;
  }
  


}
@media  screen and (max-width:700px) {
  #cp-container{
    grid-template-columns: repeat(1,1fr);
  }
  #cp-container div{
    margin-bottom: 5%;
  }

}

