.loading {
  display: flex;
 justify-content: center;
 align-items: center;
 font-size: xx-large;
 color:#026c69;

}
.loading div {
  /* position: absolute; */
  background: rgb(2, 103, 121);
  opacity: 1;
  border-radius: 50%;
  -webkit-animation: loading 1.4s cubic-bezier(0, 0.2, 0.8, 1) infinite;
          animation: loading 1.4s cubic-bezier(0, 0.2, 0.8, 1) infinite;
}
.loading div:nth-child(2) {
  -webkit-animation-delay: -.7s;
          animation-delay: -.7s;
}
@-webkit-keyframes loading {
  0% {
    top: 28px;
    left: 28px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: -1px;
    left: -1px;
    width: 58px;
    height: 58px;
    opacity: 0;
  }
}
@keyframes loading {
  0% {
    top: 28px;
    left: 28px;
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    top: -1px;
    left: -1px;
    width: 58px;
    height: 58px;
    opacity: 0;
  }
}
